@import url(https://fonts.googleapis.com/css2?family=Inter&family=Oswald&family=Roboto:wght@300&family=Zen+Kaku+Gothic+New&display=swap);
body {
  margin: 0;
  top: 0;
  font-family: 'Zen Kaku Gothic New', sans-serif;
}
.navbar {
    display: flex;
    background-color: #212121;
    top: 0;
    left: 0;
    width: 100%;
    flex-wrap: wrap;
    flex-direction: row;
}

.navbar ul {
    display: flex;
    justify-content: center;
    width: 85%;
    margin: 0;
    padding: 0;
    list-style: none;
    align-items: center;
}

.navbar ul .nav-link {
    text-decoration: none;
    color: #fff;
    padding: 14px 48px;
    cursor: pointer;
}

.navbar ul .nav-link:hover {
    background-color: #fff;
    color: #4169E1;
    box-shadow: 0px 2px 0px 0px
}

.navbar .info {
    color: #fff;
    display: flex;
    align-items: center;
}

.navbar .info img {
    width: 30px;
    height: 30px;
    border-radius: 50%;
}

.navbar .info p {
    margin-left: 10%;
}
.alert {
    text-align: center;
    margin: 0 0 10%;
    padding: 12px;
    font-size: 1.2rem;
}

.alert.alert-fail {
    color: #960018;
}

.alert.alert-success {
    color: #80c904;
}
.login {
    display: flex;
    justify-content: center;
    padding: 12%;
}

.login form {
    display: flex;
    flex-direction: column;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
    border-radius: 12px;
    padding: 64px;
}

.login form input {
    padding: 12px;
    width: 276px;
    border: 1px solid #d4d4d3;
    border-radius: 12px;
    outline-width: thin;
    outline-color: #ffd699;
    font-size: 1.1rem;
    margin: 0 0 20px;
}

.login form button {
    padding: 12px;
    border: 0;
    border-radius: 8px;
    background-color: #f198b3;
    color: #fff;
    font-size: 1.2rem;
}

.login form button:hover {
    background-color: #ec7699;
}
.register {
    display: flex;
    justify-content: center;
    padding: 12%;
    width: 512px;
    margin: auto;
}

.register form {
    display: flex;
    flex-direction: column;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
    border-radius: 12px;
    padding: 64px;
    align-items: center;
}

.register form input {
    padding: 12px;
    width: 276px;
    border: 1px solid #d4d4d3;
    border-radius: 12px;
    outline-width: thin;
    outline-color: #ffd699;
    font-size: 1.1rem;
    margin: 0 0 20px;
}

.register form button {
    padding: 12px;
    border: 0;
    border-radius: 8px;
    background-color: #f198b3;
    color: #fff;
    font-size: 1.2rem;
    width: 100%;
}

.register form button:hover {
    background-color: #ec7699;
}
.settings {
    display: flex;
    flex-direction: column;
    margin: auto;
    width: 328px;
}

.settings input {
    padding: 12px;
    width: 276px;
    border: 1px solid #d4d4d3;
    border-radius: 12px;
    outline-width: thin;
    outline-color: #4169E1;
    font-size: 1.1rem;
    margin: 0 0 20px;
}

.settings .img-container {
    display: flex;
    align-items: flex-end;
}

.settings .img-container .user-img {
    object-fit: cover;
    width: 100px;
    height: 100px;
    border-radius: 50%;
}

.settings .img-container .photo {
    outline: none;
    border: none;
    font-size: 0.8rem;
}

.settings button {
    outline: none;
    border: none;
    background-color: #4169E1;
    font-size: 1.2rem;
    color: #fff;
    padding: 12px;
    width: 100%;
    margin: 10% 0 10%;
    text-transform: uppercase;
    border-radius: 12px;
}

.loading {
    margin: auto;
}
.password {
    display: flex;
    flex-direction: column;
    margin: auto;
    width: 328px;
}

.password input {
    padding: 12px;
    width: 276px;
    border: 1px solid #d4d4d3;
    border-radius: 12px;
    outline-width: thin;
    outline-color: #4169E1;
    font-size: 1.1rem;
    margin: 0 0 20px;
}

.password button {
    outline: none;
    border: none;
    background-color: #4169E1;
    font-size: 1.2rem;
    color: #fff;
    padding: 12px;
    width: 100%;
    margin: 10% 0 10%;
    text-transform: uppercase;
    border-radius: 12px;
}

.game-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #212121;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
    width: 100%;
    margin: 0 0 5%;
}

.game-item img {
    width: 200px;
    height: 100px;
    object-fit: cover;
}

.game-item h3 {
    color: #fff;
}

.game-item p {
    color: #fff;
}

.game-item button {
    border: none;
    background-color: #212121;
    color: #960018;
    font-size: 2rem;
    margin: 0 1% 5%;
}

.choice {
    display: flex;
    justify-content: center;
}

.choice button {
    padding: 12px;
    border: none;
    outline: none;
    width: 100%;
    margin: 3%;
    font-size: 1.2rem;
    border-radius: 12px;
    text-transform: uppercase;
    color: #fff;
}

.choice .yes {
    background-color: #C70000;
}

.choice .no {
    background-color: #3CB043;
}
.games-delete h3 {
    text-align: center;
    text-transform: uppercase;
}
.add-game {
    display: flex;
    flex-direction: column;
    margin: auto;
    width: 512px;
}

.add-game input {
    padding: 12px;
    border: 1px solid #d4d4d3;
    border-radius: 12px;
    outline-width: thin;
    outline-color: #4169E1;
    font-size: 1.1rem;
    margin: 0 0 20px;
}

.add-game .photo {
    border: none;
}

.add-game button {
    outline: none;
    border: none;
    background-color: #4169E1;
    font-size: 1.2rem;
    color: #fff;
    padding: 12px;
    width: 100%;
    margin: 10% 0 10%;
    text-transform: uppercase;
    border-radius: 12px;
}

.users .header {
    display: flex;
    width: 100%;
    justify-content: space-around;
    align-items: center;
    margin: auto;
    border: solid 0.5px rgba(0, 0, 0, 0.1);
}

.profile {
    display: flex;
    padding: 5%;
    justify-content: center;
    height: 100%;
}

.profile .nav-list {
    width: 15%;
    background-color: #212121;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}

.profile .nav-list ul {
    display: flex;
    flex-direction: column;
    list-style: none;
    margin: 0;
    padding: 0;
}

.profile .nav-list ul .nav-item {
    text-transform: uppercase;
    padding: 5% 15%;
    color: #fff;
    font-size: 1.2rem;
}

.profile .nav-list ul .nav-item.active {
    background-color: #fff;
    color: #4169E1;
    box-shadow: 0px 2px 0px 0px
}

.profile .nav-list ul .nav-item:hover {
    background-color: #fff;
    color: #4169E1;
    box-shadow: 0px 2px 0px 0px
}

.profile .content {
    width: 40%;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
    padding: 3%;
}

.slider {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
}
.game-card {
    display: flex;
    flex-direction: column;
    width: 20%;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
    background-color: #212121;
    color: #fff;
    margin: 3%;
}

.game-card:hover {
    background-color: #333333;
    color: rgb(228, 224, 224);
    box-shadow: 0px 2px 0px 0px
}

.game-card img {
    width: 100%;
    height: 512px;
    object-fit: cover;
}

.game-card h1 {
    text-align: center;
}

.game-card i {
    cursor: pointer;
}
.all-games {
    display: flex;
    margin: auto;
    flex-wrap: wrap;
    justify-content: center;
    width: 1980px;
}
.games-db {
    display: flex;
    background-color: #212121;
    color: #fff;
    width: 40%;
    padding: 10px;
    margin: 0 0 20px;
}

.games-db img {
    width: 320px;
    height: 160px;
    object-fit: cover;
}

.games-db .games-info {
    margin-left: 5%;
}

.games-db .games-info button {
    padding: 6px;
    border: none;
    outline: none;
    border-radius: 12px;
    background-color: #960018;
    color: #fff;
    cursor: pointer;
}
.mygames {
    display: flex;
    padding: 20px;
    margin: auto;
    flex-direction: column;
    align-items: center;
}
footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #555555;
    color: #fff;
}

footer .social ul {
    list-style: none;
    display: flex;
}

footer .social ul li {
    font-size: 2rem;
    margin: 0 25px 0;
    cursor: pointer;
}

footer .social ul li:hover {
    color: #4169E1;
    box-shadow: 0px 2px 0px 0px
}

footer .rules ul {
    list-style: none;
    display: flex;
}

footer .rules ul li {
    cursor: pointer;
    font-size: 1.2rem;
    margin: 0 20px 0;
}


footer .rules ul li:hover {
    box-shadow: 0px 2px 0px 0px
}


